<script setup lang="ts">
import { TrashIcon, DocumentDuplicateIcon } from '@heroicons/vue/20/solid';
import {
    DropdownMenu,
    DropdownMenuContent,
    DropdownMenuItem,
    DropdownMenuTrigger,
} from '@/Components/ui/dropdown-menu';
import type { TimeEntry } from '../../../api/src';
const props = defineProps<{ entry?: TimeEntry; haveduplicate?: boolean }>();

const emit = defineEmits<{
    delete: [];                // no payload
    duplicate: [TimeEntry];    // payload is a TimeEntry
}>();
function onCopyClick() {
    props.entry && emit('duplicate', props.entry);   // ðŸ‘ˆ send the whole entry upward
}
</script>

<template>
    <DropdownMenu>
        <DropdownMenuTrigger as-child>
            <button
                class="focus-visible:outline-none focus-visible:bg-card-background rounded-full focus-visible:ring-2 focus-visible:ring-ring focus-visible:opacity-100 hover:bg-card-background group-hover:opacity-100 opacity-20 transition-opacity text-text-secondary"
                aria-label="Actions for the time entry">
                <svg class="h-8 w-8 p-1 rounded-full" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
                    <path fill="none" stroke="currentColor" stroke-linecap="round" stroke-linejoin="round"
                        stroke-width="1.5"
                        d="M12 5.92A.96.96 0 1 0 12 4a.96.96 0 0 0 0 1.92m0 7.04a.96.96 0 1 0 0-1.92a.96.96 0 0 0 0 1.92M12 20a.96.96 0 1 0 0-1.92a.96.96 0 0 0 0 1.92" />
                </svg>
            </button>
        </DropdownMenuTrigger>
        <DropdownMenuContent class="min-w-[150px] dark:bg-[#0F1426] bg-gray-100 dark:border-[#303F61]" align="end">
            <DropdownMenuItem v-if="haveduplicate" data-testid="time_entry_duplicate"
                class="flex items-center space-x-3 cursor-pointer text-destructive focus:text-destructive hover:dark:bg-[#171E31] hover:dark:text-[#BFC7D6] dark:text-[#636E88]"
                @click="onCopyClick">
                <DocumentDuplicateIcon class="w-5" />
                <span>Duplicate</span>
            </DropdownMenuItem>
            <DropdownMenuItem data-testid="time_entry_delete"
                class="flex items-center space-x-3 cursor-pointer text-destructive focus:text-destructive  hover:dark:bg-[#171E31] hover:dark:text-[#BFC7D6] dark:text-[#636E88] "
                @click="emit('delete')">
                <TrashIcon class="w-5" />
                <span>Delete</span>
            </DropdownMenuItem>
        </DropdownMenuContent>
    </DropdownMenu>
</template>

<style scoped></style>
